import React from "react";
import key from "../../../util/key";
import { injectIntl } from "react-intl";
import { Link, withRouter } from "react-router-dom";
import { Tree, Space, message, Button } from "antd";
import Layout4 from "../../../util/Layout4";
import regexEscape from "../../../util/regexEscape";
import Context from "../../../Context";
import paramsHasChanged from "../../../util/paramsHasChanged";
import { FallOutlined, MenuOutlined, CopyOutlined } from "@ant-design/icons";
import Title from "../../Admin/Title";
import OcppCommandsForm from "./OcppCommandsForm";

class OcppCommands extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      treeSearch: "",
      treeKey: key(),
      tree: [],
      stations: [],
      identity: null,
      command: null,
      commandResult: null,
    };
  }
  pendingStart = () => {
    if (this.props.onPendingStart) {
      this.props.onPendingStart();
      return;
    }
    this.setState({ pending: true });
  };
  pendingEnd = () => {
    if (this.props.onPendingEnd) {
      this.props.onPendingEnd();
      return;
    }
    this.setState({ pending: false });
  };
  componentDidMount() {
    this.setParams();
    this.load();
  }

  componentDidUpdate(prevProps) {
    if (paramsHasChanged(prevProps, this.props)) {
      this.setParams();
    }

    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.load();
    }
  }
  setParams = () => {
    const { param1: identity, param2: command } = this.props.match.params;
    this.setState({
      identity,
      command,
      commandResult: null,
    });
  };

  load = async () => {
    this.setState({ pending: true });
    let stations = this.context.ocpp.getArray(
      await this.context.ocpp.q("/ChargePointList", {}),
      "ChargePointList",
      "Identity"
    );

    let tree = Object.entries(this.context.ocpp.commands).map(([k]) => {
      return { key: k, title: this.m(k) };
    });

    this.setState({ treeKey: key(), tree, stations, pending: false });
  };

  getStation = (identity) => {
    return this.state.stations.find((station) => station.Identity === identity);
  };

  onTreeSelect = (selectedKeys) => {
    if (Array.isArray(selectedKeys)) {
      if (selectedKeys[0]) {
        this.props.history.push(
          `/ocpp/command/${this.state.identity}/${selectedKeys[0]}/`
        );
      }
    }
  };
  onTreeSearch = (value) => {
    this.setState({ treeSearch: value });
  };

  onFormFinish = async (data) => {
    const { command, identity } = this.state;
    this.pendingStart();

    let dataToSend = data;
    Object.keys(dataToSend).forEach(field => {
      if (field === 'reservationId' || field === 'transactionId' || field === 'connectorId') {
        dataToSend[field] = Number(dataToSend[field])
      }
    })

    let commandResult = await this.context.ocpp.q(
      `/ChargePoint/${identity}/${command}`,
      dataToSend
    );
    this.setState({ commandResult });
    this.pendingEnd();
  };

  m = (key, prefix = "ocpp.commands.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };

  render() {
    const {
      pending,
      treeKey,
      identity,
      command,
      commandResult,
      tree,
      treeSearch,
    } = this.state;
    const station = this.getStation(identity);

    let treeData = !treeSearch
      ? tree
      : tree.filter((item) => {
          let reg = new RegExp(regexEscape(treeSearch), "gi");
          return (item.title.toString().match(reg) || []).length > 0;
        });

    return (
      <>
        <Title
          title={this.m("commands")}
          url="/ocpp/command"
          icon={<FallOutlined />}
        />
        <Layout4 pending={pending} onSearch={this.onTreeSearch} searchDelay={0}>
          <div>
            {station ? (
              <Space>
                <Link to="/ocpp">
                  <h3 className="gx-mb-0">{station.Identity}</h3>
                </Link>
                <div className="gx-text-grey">{station.Address}</div>
              </Space>
            ) : (
              <>{!pending && <Link to="/ocpp">{this.m("pick")}</Link>}</>
            )}
          </div>
          <div className="isoNoteBtns">
            <Space>
              <Link to="/ocpp">
                <Button
                  onClick={this.onAddStart}
                  type="secondary"
                  icon={<MenuOutlined />}
                />
              </Link>
            </Space>
          </div>

          <Tree
            key={treeKey}
            defaultExpandParent
            defaultSelectedKeys={[command]}
            defaultExpandedKeys={[command]}
            treeData={treeData}
            onSelect={this.onTreeSelect}
          />

          <div>
            <OcppCommandsForm
              m={this.m}
              onFinish={this.onFormFinish}
              station={station}
              command={command}
            />
            {commandResult && (
              <div className="inspector">
                <hr />
                <h3>
                  {this.m("result")}
                  <Button
                    className="gx-ml-2 gx-mb-0"
                    onClick={() => {
                      navigator.clipboard.writeText(
                        JSON.stringify(commandResult, null, 2)
                      );
                      message.success(this.m("copied"));
                    }}
                    icon={<CopyOutlined />}
                  />
                </h3>
                <pre>{JSON.stringify(commandResult, null, 2)}</pre>
              </div>
            )}
          </div>
        </Layout4>
      </>
    );
  }
}

export default withRouter(injectIntl(OcppCommands));
